<template>
    <v-container fluid class="fill-height">
        <v-row justify="center" align="center" dense>
            <v-col class="text-center" sm="12" md="4">
                <v-card outlined class="py-5">
                    <v-card-text>
                        <v-avatar color="grey" size="200">
                            <v-img src="https://w5insight.com/wp-content/uploads/2014/07/placeholder-user-400x400.png"/>
                        </v-avatar>
                        <div class="my-5">
                            <h2 class="title">{{detail == null ? user.username : detail.name}}</h2>
                            <span class="subtitle-1" v-if="detail">NIK : {{detail.id_number}}</span>
                        </div>
                        <v-btn depressed color="warning" block @click="$router.push({name:'start-demo'})" style="margin-bottom:10px">Uji Coba</v-btn>
                        <v-btn depressed color="red" block @click="redirectTo('tutorial')" style="margin-bottom:10px">Tutorial</v-btn>
                        <v-btn depressed color="primary" block v-if="download" @click="redirectTo('download')" style="margin-bottom:10px">Download Laporan</v-btn>
                        <template v-if="detail">
                            <v-btn depressed color="success" block @click="start" v-if="detail.exam && !detail.examParticipant.status">Mulai Tes</v-btn>
                            <v-btn depressed color="success" block @click="lanjut" v-else-if="detail.exam && detail.examParticipant.status == 'start'">Lanjutkan Tes</v-btn>
                            <v-btn text color="success" block v-else-if="detail.exam && detail.examParticipant.status == 'finish'">Anda sudah menyelesaikan tes</v-btn>
                            <v-btn text color="warning" block v-else>Tidak ada tes</v-btn>
                        </template>
                        <template v-else>
                            <v-btn text color="warning" block>Tidak ada tes</v-btn>
                        </template>
                        <v-btn text color="red" @click="logout()" block>Keluar</v-btn>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import {mapState,mapMutations,mapActions} from 'vuex'
import axios from 'axios'

export default {
  name: 'Dashboard',
  data(){
      return{
          seqActive : 0,
          url:'',
          download:'',
          tutorial:'',
      }
  },
  async created(){
      this.url = process.env.VUE_APP_URL_BASE
      var request = await axios.get(process.env.VUE_APP_URL+'data')
      if(request.status == 200)
      {
          this.tutorial = request.data.tutorial
          this.download = request.data.download
      }
  },
  computed:{
    ...mapState({
        loading: state => state.loading,
        user : state => state.userLoggedIn,
        detail : state => state.detail,
        categories : state => state.categories,
    }),
  },
  methods:{
    ...mapMutations(['setLoading']),  
    ...mapActions(['getDetail','startExam']),
    redirectTo(param)
    {
        if(param == 'tutorial')
            window.location=this.tutorial
        else
            window.location=this.download
    },
    start(){
        // this.startExam()
        this.$router.push({name:'start'})
    },
    lanjut(){
        this.$router.push({name:'sequence'})
        location.reload()
    },
    downloadLaporan(){
        window.open(this.url+'site/laporan?token='+this.user.auth_key)
    },
    logout(){
        this.$store.dispatch('logout')
        this.$router.push({'name':'login'})
    }
  }
}
</script>
