import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'querystring'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userLoggedIn: JSON.parse(localStorage.getItem("userLoggedIn")),
    categories:JSON.parse(localStorage.getItem("categories")),
    demo_categories:[],
    answered:JSON.parse(localStorage.getItem("answered")),
    detail:JSON.parse(localStorage.getItem("detail")),
    loading:false,
    login_reload:false
  },
  mutations:{
    setLoading(state,load){
      state.loading = load
    },
    SET_USER: (state,value) => state.userLoggedIn = value,
    SET_DETAIL: (state,value) => state.detail = value,
  },
  actions:{
    setUser({commit}, val){
      commit('SET_USER',val)
    },
    setDetail({commit}, val){
      commit('SET_DETAIL',val)
    },
    getCategories(state, auth_key){
      return axios
          .get(process.env.VUE_APP_URL+'categories',{
              headers: {
                  'Authorization':'Bearer '+auth_key
              }
          })
    },
    getDemoCategories(){
      return axios.get(process.env.VUE_APP_URL+'demo-categories')
    },
    getLastCategory(state, data){
      return axios
          .get(process.env.VUE_APP_URL+'last-category?exam_id='+data.exam_id,{
              headers: {
                  'Authorization':'Bearer '+data.auth_key
              }
          })
          // .then(res => {
          //     localStorage.setItem('lastCategory',JSON.stringify(res.data))
          //     state.loading = false
          // })
    },
    getAnswered(state, auth_key){
      return axios
          .get(process.env.VUE_APP_URL+'answered',{
              headers: {
                  'Authorization':'Bearer '+auth_key
              }
          })
          // .then(res => {
          //     localStorage.setItem('answered',JSON.stringify(res.data))
          //     state.loading = false
          // })
    },
    login : (state, user) => {
      return axios
            .post(process.env.VUE_APP_URL+'login',qs.stringify(user),{
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                }
            });
            // .then((res) => {
            //     state.loading = false
            //     window.localStorage.setItem('userLoggedIn',JSON.stringify(res.data))
            //     dispatch('getDetail',res.data.auth_key)
            // })
    },
    logout : ({commit}) => {
      // axios
      // .get(process.env.VUE_APP_URL+'logout',{
      //   headers:{
      //       'Authorization':'Bearer '+state.userLoggedIn.auth_key
      //   }
      // })
      // .then(() => {
        commit('SET_USER',{})
        localStorage.clear()
      // })
        
    },
    getDetail:(state, auth_key)=>{
        return axios
        .get(process.env.VUE_APP_URL+'detail',{
            headers:{
                'Authorization':'Bearer '+auth_key
            } 
        });
        // .then(res=>{    
        //     localStorage.setItem('detail',JSON.stringify(res.data))
        //     if(res.data.exam != null){
        //       dispatch('getAnswered',auth_key)
        //       dispatch('getLastCategory',{auth_key:auth_key,exam_id:res.data.exam.id})
        //       dispatch('getCategories',auth_key)
        //     }
        // })
    },
    setAnswer:({state},data) => {
      axios
          .post(process.env.VUE_APP_URL+'answer',qs.stringify(data),{
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded',
                  'Authorization':'Bearer '+state.userLoggedIn.auth_key
              }
          })
    },
    startExam:({state},params) => {
      return new Promise((resolve) => {
        axios
        .post(process.env.VUE_APP_URL+'start',qs.stringify(params),{
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization':'Bearer '+state.userLoggedIn.auth_key
          }
        }).then(res=>{
          resolve(res)
        })
      });
      
    },
    finishExam:({state}) => {
      axios
        .get(process.env.VUE_APP_URL+'finish',{
          headers: {
              'Authorization':'Bearer '+state.userLoggedIn.auth_key
          }
        }).then(res=>{
          localStorage.setItem('detail',JSON.stringify(res.data.detail))
        })
    },
    nextExam:({state},data) => {
      axios
        .post(process.env.VUE_APP_URL+'next',qs.stringify(data),{
          headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
              'Authorization':'Bearer '+state.userLoggedIn.auth_key
          }
        })
    }
  },
})
