import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard'
import Login from '../views/Login'
import Sequence from '../views/Sequence'
import SequenceDemo from '../views/SequenceDemo'
import Start from '../views/Start'
import StartDemo from '../views/StartDemo'
import Finish from '../views/Finish'
import FinishDemo from '../views/FinishDemo'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'dashboard',
    component: Dashboard,
    beforeEnter: (to,from,next) => {
      var user = window.localStorage.getItem('userLoggedIn')
      if(!user)
        next({'name':'login'})
      next();
    }
  },
  {
    path: '/login',
    name: 'login',
    component:Login,
    beforeEnter: (to,from,next) => {
      var user = window.localStorage.getItem('userLoggedIn')
      console.log(user)
      if(user)
      {
        next({'name':'dashboard'})
      }
      next();
    }
  },
  {
    path: '/sequence/',
    name: 'sequence',
    component:Sequence
  },
  {
    path: '/sequence-demo/',
    name: 'sequence-demo',
    component:SequenceDemo
  },
  {
    path: '/start',
    name: 'start',
    component:Start
  },
  {
    path: '/start-demo',
    name: 'start-demo',
    component:StartDemo
  },
  {
    path: '/finish',
    name: 'finish',
    component:Finish
  },
  {
    path: '/finish-demo',
    name: 'finish-demo',
    component:FinishDemo
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router
