<template>
    <v-container>
        <v-form ref="form" @submit.prevent="start" id="form-start">
            <v-row justify="center">
                <v-col cols="12" md="6">
                    <v-row dense>
                        
                    </v-row>
                    <v-row dense>
                        <v-col cols="12">
                            <p class="title">Informasi Pribadi</p>
                        </v-col>
                        
                        <v-col v-for="(meta,idx) in person_information" :key="idx" cols="12">
                            <v-select v-if="meta.options" outlined dense :label="meta.label" :rules="rules" :items="meta.options" v-model="meta.value"/>
                            <template v-else-if="meta.ttl">
                            <v-menu
                                ref="menu"
                                v-model="menu"
                                :close-on-content-click="false"
                                :return-value.sync="meta.value"
                                transition="scale-transition"
                                offset-y
                                min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="meta.value"
                                    label="Tanggal Lahir"
                                    prepend-icon="mdi-calendar"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                                </template>
                                <v-date-picker
                                v-model="meta.value"
                                no-title
                                scrollable
                                >
                                <v-spacer></v-spacer>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="menu = false"
                                >
                                    Cancel
                                </v-btn>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="saveDate($event)"
                                >
                                    OK
                                </v-btn>
                                </v-date-picker>
                            </v-menu>
                            </template>
                            <v-text-field v-else dense outlined v-model="meta.value" :loading="loading" :rules="rules" :label="meta.label" />
                        </v-col>

                        <v-col cols="12" class="text-center">
                            <v-btn depressed class="primary" type="submit" form="form-start" :loading="loading" block>Submit</v-btn>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-form>
    </v-container>
</template>

<script>
    import { mapState } from 'vuex'
    
    export default {
        name:'StartDemo',
        data(){
            return{
                person_information:{
                    nama_lengkap:{
                        label:'Nama Lengkap',
                        value:'',
                    },
                    tempat_lahir:{
                        label:'Tempat Lahir',
                        value:'',
                    },
                    tanggal_lahir:{
                        label:'Tanggal Lahir',
                        value:new Date().toISOString().substr(0, 10),
                        ttl:true,
                    },
                    NIK:{
                        label:'NIK',
                        value:'' ,
                    },
                    jenis_kelamin:{
                        label:'Jenis Kelamin',
                        value:'',
                        options:['Laki-Laki','Perempuan']
                    },
                    pendidikan_terakhir:{
                        label:'Pendidikan Terakhir',
                        value:'',
                    },
                    lama_bekerja:{
                        label:'Lama Bekerja',
                        value:'',
                    },
                    pelajaran:{
                        label:'Bidang Studi',
                        value:'',
                    },
                },
                rules : [
                    v => !!v || 'This field is required'
                ],
            }
        },
        created(){
            // this.userDetail()
            this.person_information.nama_lengkap.value = this.detail.name
            this.person_information.pelajaran.value = this.detail.study
            this.person_information.lama_bekerja.value = this.detail.work_time
        },
        computed:{
            ...mapState({
                loading : state => state.loading,
                user: state => state.userLoggedIn,
                detail : state => state.detail,
                // otherData : state => state.auth.otherData,
            })
        },
        methods:{
            start(){
                if(this.$refs.form.validate()){
                    this.$router.push({name:'sequence-demo'})
                }
            },
            addItem(item){
                const newItem = new Object({
                    nama:{
                        label:'Nama',
                        value:'',
                    },
                    tahun:{
                        label:'Tahun',
                        value:'',
                    },
                    hasil:{
                        label:'Hasil',
                        value:'',
                    },
                })
                item.value.push(newItem)
            }
        },
    }
</script>

<style lang="scss" scoped>

</style>