<template>
    <div style="background:#eaeaea;" class="fill-height" v-if="data">
        <v-col class="text-center" style="position: fixed;z-index: 1;" v-if="data.has_timer != 'No'">
            <v-chip color="primary">{{count}}</v-chip>
        </v-col>
        <v-container>
            <div style="padding-bottom: 50px"></div>
            <v-row justify="center" v-if="loading">
                <v-col sm="12" md="6" class="text-center">
                    <v-progress-circular
                        indeterminate
                        color="primary"
                    ></v-progress-circular>
                </v-col>
            </v-row>
            <v-row justify="center" v-else>
                <v-col sm="12" md="6">
                    <v-row dense>
                        <v-col cols="12">
                            <v-card outlined>
                                <v-card-title class="justify-center">{{data.name}}</v-card-title>
                            </v-card>
                        </v-col>
                        <v-col cols="12" v-for="post in data.posts" :key="post.id">
                            <v-card outlined>
                                <v-card-text>
                                    <p class="subtitle" v-html="post.post_content"></p>
                                    <div v-if="data.name == 'CFIT 2'">
                                        <v-text-field maxlength="2" outlined dense placeholder="Masukkan jawabannya.." @input="selectedAnswer(post.id)" v-model="answered[post.id]"></v-text-field>
                                    </div>
                                    <div v-else>
                                        <template v-if="post.items">
                                        <v-radio-group @change="selectedAnswer(post.id)" dense v-model="answered[post.id]">
                                            <v-radio v-for="item in post.items" :key="item.id" :value="item.id" class="d-flex align-items-center">
                                                <template v-slot:label>
                                                    <span v-html="item.post_content" style="width:100%"></span>
                                                </template>
                                            </v-radio>
                                        </v-radio-group>
                                        </template>
                                    </div>
                                </v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-row dense>
                        <v-col class="text-right">
                            <v-btn @click="next" depressed color="primary" block>
                                {{index == categories.length-1 ? 'Selesai' : 'Selanjutnya'}} <v-icon>{{index == categories.length-1 ? 'mdi-check' :'mdi-arrow-right'}}</v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
// import Swal from 'sweetalert2'
    export default {
        name:'Sequence',
        data(){
            return{
                index:0,
                interval:null,
                data:{},
                counts:[],
                count:"00:00:00",
                time:0
            }
        },
        async created(){
            // await this.getLastCategory({exam_id:this.detail.exam.id,auth_key:this.userLoggedIn.auth_key})
            var lastCategory = JSON.parse(localStorage.getItem("lastCategory"))
            if(lastCategory)
            {
                this.index = parseInt(lastCategory.category.sequenced_number)-1
                // this.counts = lastCategory.time_left
                this.data = this.categories[this.index]
                this.data.countdown = lastCategory.time_left
            }
            else
                this.data = this.categories[this.index]
            if(this.data.has_timer) // == "Countdown"){
                this.countdown()
            // }
        },
        computed:{
            ...mapState({
                loading:state=>state.loading,
                categories:state=>state.categories,
                detail:state=>state.detail,
                userLoggedIn:state=>state.userLoggedIn,
                answered:state=>state.answered
            }),
        },
        watch:{
            data(val){
                if(val.has_timer == "Countdown"){
                    this.counts = val.countdown.split(':')
                    this.time = parseInt(this.counts[2])
                    this.time += parseInt(this.counts[1])*60
                    this.time += (parseInt(this.counts[0])*60)*60
                }else if(val.has_timer == "Ticker"){
                    this.counts = val.countdown ? val.countdown.split(':') : [0,0,0]
                }
            }
        },
        methods:{
            ...mapActions(['setAnswer','finishExam','nextExam','getLastCategory']),
            async selectedAnswer(question_id){
                await this.setAnswer({exam_id:this.detail.exam.id,question_id:question_id,answer_id:this.answered[question_id]})
                await localStorage.setItem("answered",JSON.stringify(this.answered))

                // if(this.data.has_timer != "No"){
                    await this.nextExam({exam_id:this.detail.exam.id,category_id:this.data.id,time_left:this.count})
                // }
            },
            countdown(){
                if(this.data.has_timer == "Countdown"){
                    this.interval = setInterval(()=>{ 
    
                        if(this.counts[1] < 0 && this.counts[0] > 0){
                            this.counts[0] -= 1
                            this.counts[1] = 59
                        }
    
                        if(this.counts[2] < 0 && this.counts[1] > 0){
                            this.counts[1] -= 1
                            this.counts[2] = 59
                        }
    
                        if(this.time == 0){
                            this.next()
                        }else{
                            this.count = `${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`
                        }
    
                        this.counts[2]--;
    
                        this.time--;

                        localStorage.setItem('lastCategory',JSON.stringify({
                            time_left:`${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`,
                            category:this.data
                        }))
    
                    },1000)
                }else if(this.data.has_timer == "Ticker"){

                    this.interval = setInterval(()=>{
                        if(this.counts[2] > 59){
                            this.counts[1] = parseInt(this.counts[1])+1
                            this.counts[2] = 0
                        }

                        if(this.counts[1] > 59){
                            this.counts[0] = parseInt(this.counts[0])+1
                            this.counts[1] = 0
                        }
                        
                        this.count = `${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`

                        this.counts[2]++;
                        this.time++;

                        localStorage.setItem('lastCategory',JSON.stringify({
                            time_left:`${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`,
                            category:this.data
                        }))
                        
                    },1000)

                }
            },
            async next(){
                window.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });

                if([9,10,11].includes(this.index))
                {
                    var haveNull = false
                    
                    // console.log(this.answered)
                    for(var i=0;i<this.data.posts.length;i++){
                        if(this.answered[this.data.posts[i].id]==undefined)
                        {
                            haveNull = true
                            break
                        }
                        
                    }
                    if(haveNull) {
                        alert("Terdapat jawaban yang belum di pilih")
                        return
                    }
                }

                if(this.index == this.categories.length-1){
                    await this.finish()
                }else{
                    await clearInterval(this.interval)
                    
                    // if(this.data.has_timer != "No"){
                        await this.nextExam({exam_id:this.detail.exam.id,category_id:this.data.id,time_left:this.count})
                    // }

                    this.index++
                    this.data = this.categories[this.index]
                    this.countdown()
                    // await this.sendUserSequence({sequences:this.sequences,seqActive:this.index})
                    // if(this.data.has_timer != "No"){
                        await this.nextExam({exam_id:this.detail.exam.id,category_id:this.data.id,time_left:this.count})
                        localStorage.setItem('lastCategory',JSON.stringify({
                            time_left:`${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`,
                            category:this.data
                        }))
                    // }
                }
            },
            async finish(){ 
                var c = confirm("Apakah anda yakin telah menyelesaikan ujian?")
                if(c)
                {
                    await clearInterval(this.interval)
                    await this.finishExam()
                    this.$router.push({name:'finish'})
                }
                
            },
        },
    }
</script>

<style>
    p img {
        max-width: 100% !important;
    }
    input[type=text] {
        text-transform: uppercase;
    }
</style>